@import "../../../style/theme/default/var.css";
@component-namespace activity{
  @b wrap {
    @e calendar {
      & .el-calendar__date-content {
          overflow-y: inherit;
      }
    & td.next-month,
    & td.prev-month {
        .activity-list__badge > span {
          background-color: color(var(--theme-font-color-secondary) a(0.4));
        }
      .activity-list__status-list span.item .name,
      .activity-list__status-list span.item .num {
          color: var(--theme-font-color-secondary);
        }
      }
    }
/*数据统计*/
  @e record {
    text-align: center;
        & .el-col{
        border:1px solid var(--theme-border-color-primary);
        border-right: 0;
        padding: var(--theme-padding-middle);
        &:last-child{
           border-right: 1px solid var(--theme-border-color-primary);
         }
     & .record-name{
       font-size: var(--theme-font-size-small);
        margin-bottom: var(--theme-margin-base);
       }
  & .record-num{
      font-size: var(--theme-font-size-small);
    b{
      font-size: 20px;
    }
    }
      }
  }
/* /end 数据统计*/

  @e header{
    margin-top: 0;
    text-align: center;
    line-height: 30px;
  @utils-clearfix;
  }
}
  @b list{
    /* 弹出浮动 */
    @e popover{
      &.el-popover{
        padding:0;
        border:0;
      }
    }
    @e popover-header{
      padding: var(--theme-padding-base);
      background: var(--theme-color-primary);
      line-height: 20px;
      font-size:var(--theme-font-size-small);
      color: var(--color-white);
      & i{
        float:right;
        font-size: var(--theme-font-size-middle);
        cursor: pointer;
      }
    }
    /* /end 弹出浮动 */

    /* popover里面的折叠面板 */
    @e collapse{
      & .el-collapse-item__header{
      font-size: var(--theme-font-size-small);
          height: 36px;
          line-height: 36px;
          padding: 0 var(--theme-padding-base);
          &  b{
            font-weight: normal;
            color: var(--theme-color-primary);
            margin-right: var(--theme-margin-base);
          }
        }
      & .el-collapse-item__header__arrow:before{
          line-height: 36px;
      }
      & .el-collapse-item__content{
       padding: var(--theme-padding-base);
      }
    }
    /*/end  popover里面的折叠面板 */
    /* 圆角 数字标签 */
    @e badge{
        position: relative;
        height: 15px;
        & >span{
         position: absolute;
         top:-15px;
         left: 50%;
         transform: translate(-50%, 0);
         -webkit-transform: translate(-50%, 0);
         -moz-transform: translate(-50%, 0);
        background-color: var(--theme-color-primary);
        color: var(--color-white);
        border-radius: 100%;
        font-size: var(--theme-font-size-middle);
        display: inline-block;
        width: 32px;
        height: 32px;
        text-align: center;
        line-height: 32px;
        & b{
            font-size: 12px;
            font-weight: normal;
          }
      }
    }
    /* /end  圆角 数字标签 */
  /* /  活动名称列表 */
  @e article{
    display: block;
    @utils-ellipsis;
    height:20px;
    font-size: var(--theme-font-size-small);
    color: var(--theme-font-color-primary);
    cursor: pointer;
    &:hover{
      color: var(--theme-color-primary);
    }
  }
  /* /end 活动名称列表 */
/*  状态列表 */
  @e status{
  /*  margin-top: var(--theme-margin-middle);*/
  }
@e status-list{
    margin-top: var(--theme-margin-middle);
  & span.item{
    float: left;
    width:25%;
    text-align: center;
     color: var(--theme-font-color-primary);
    & .name{
        display: block;
        font-size: var(--theme-font-size-small);
        margin-bottom: 5px;
       color: var(--theme-font-color-primary);
      }
    & .num {
      font-weight: bold;
      font-size: var(--theme-font-size-base);
    }
  }
}

/* /end 状态列表 */
  }
}


@media (max-width: 1680px){
  .activity-list__badge >span{
    top: -18px;
  }
  .activity-wrap__calendar .el-calendar__date {
    height: 145px;
  }
  .activity-list__status-list {
    margin-top: 0;
    & span.item {
        width: 50%;
        margin-bottom: 5px;
      & .name{
          margin-bottom: 0;
        }
      & .num{
          /*font-size: 12px;*/
      }
    }
  }
}

@media (max-width: 1366px){
  .activity-list__badge >span{
    top: -20px;
  }
  .activity-list__status-list {
    margin-top: 0;
  & span.item {
      width: 100%;
      margin-bottom: var(--theme-margin-small);
    text-align: left;
    padding:0 var(--theme-padding-small);
    & .name{
        margin-bottom: 0;
  display: inline-block;
      }
      &  .num {
  float: right;
         }
   }
}
}



